.detail {
  padding: 0.2em;
}

.description {
  white-space: pre-wrap;
}

@media screen and (max-width: 400px) {
  .detail {
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .video {
    margin: auto;
    width: 100vw;
    height: 40vh;
  }
}
