.app {
  max-width: 80rem;
}

.content {
  display: flex;
}

.detail {
  flex: 1 1 70%;
}

.list {
  flex: 1 1 30%;
}

@media screen and (max-width: 48rem) {
  .content {
    flex-direction: column;
    align-items: center;
  }
  .app {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
